  width: 100%;
}
.info__box > * {
  grid-area: 1/1/2/2;
}
.info img {
  width: 100%;
}
.info__front, .info__back {
  backface-visibility: hidden;
  transition: transform 0.3s ease;
}
.info__front {
  transform: perspective(600px) rotateY(0deg);
}
.info__back {
  transform: perspective(600px) rotateY(180deg);
}
.info__box:hover > .info__front {
  transform: perspective(600px) rotateY(-180deg);
}
.info__box:hover > .info__back {
  transform: perspective(600px) rotateY(0deg);
}
.info__text {
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 112%;
  letter-spacing: 0.32px;
}
@media (max-width: 1023px) {
  .info__text {
    margin: 0 0 4px;
  }
}
.info__bag {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.info__price {
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 100%;
}
.info__price span {
  position: relative;
  top: 1px;
  left: 3px;
  font-size: 18px;
}
.info__btn {
  width: 80px;
  height: 32px;
  padding: 6px 0;
  flex-shrink: 0;
  border-radius: 8px;
  background-color: #ffffff;
  transition: background-color 0.3s ease;
}
@media (max-width: 1023px) {
  .info__btn {
    width: 40px;
    height: 24px;
    padding: 4px 12px;
    border-radius: 6px;
    background-color: #f2f2f2;
  }
}
.info__item path {
  transition: stroke 0.3s ease;
}
@media (max-width: 1023px) {
  .info__item path {
    stroke: #1f2020;
  }
}
.info__item:hover .info__btn {
  opacity: 1;
  background-color: #7bb899;
}
.info__item:hover path {
  stroke: #1f2020;
}

.footer {
  background: #000000;
  min-height: 312px;
}
@media (max-width: 1023px) {
  .footer {
    min-height: 204px;
  }
}

.overlay {
  position: fixed;
  top: 0;
  width: 100%;
  height: 0;
  background: #000000;
  opacity: 0.7;
}
.overlay--active {
  height: 100vh;
}